
.tap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding-bottom: 5px;
}
.tap .tap-items {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 25%;
  text-align: center;
}
.tap .tap-items a {
  padding: 4px 5px;
}
.tap .tap-items a.router-link-active {
  color: #ffcd32;
  border-bottom: 2px solid #ffcd32;
}


