
.header {
  width: 100%;
  position: relative;
  text-align: center;
  font-size: 18px;
  height: 44px;
  line-height: 44px;
}
.header .icon {
  display: inline-block;
  width: 30px;
  height: 32px;
  background-image: url("default@2x.png");
  background-size: 30px 32px;
  background-repeat: no-repeat;
  margin: 6px 6px 0 0;
  vertical-align: top;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .icon {
    background-image: url("default@3x.png");
}
}
.header .tit {
  display: inline-block;
  height: 44px;
  vertical-align: top;
}


