
.slide {
  min-height: 1px;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.slide .slide-group {
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  font-size: 0;
}
.slide .slide-group .slider-item {
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
.slide .slide-group .slider-item a {
  display: inline-block;
  width: 100%;
  text-decoration: none;
}
.slide .slide-group .slider-item a img {
  display: block;
  width: 100%;
}
.slide .dots {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 12px;
  -webkit-transform: translateZ(1px);
          transform: translateZ(1px);
  text-align: center;
  font-size: 0;
}
.slide .dots .dot {
  display: inline-block;
  margin: 0 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: rgba(255,255,255,0.5);
}
.slide .dots .dot.active {
  width: 20px;
  border-radius: 5px;
  background-color: #fff;
}


